import back from '../../components/back.vue';
export default {
	data() {
		return {
			pageNameFlag:1,
			showFlag:0,
			pageName:'手机快速注册',
			phoneNum:'',
			passwordFlag:true,
			againFlag:true,
			password:'',
			again:'',
			seeFlag:0,
			seeAgainFlag:0,
			loadingFlag:false,
			doneFlag:0,
			errorMsg:'',
			disabledFlag:false
		}
	},
	methods: {
		setPassword(e){
			var that = this;
			if(e.detail.value.length>0){
				that.seeFlag=1;
				that.password=e.detail.value;
				if(e.detail.value.length>6&&that.again.length>6){
					that.doneFlag=1;
				}else{
					that.doneFlag=0;
				}
			}else{
				that.seeFlag=0;
				that.password='';
			}
		},
		setAgain(e){
			var that = this;
			if(e.detail.value.length>0){
				that.seeAgainFlag=1;
				that.again=e.detail.value;
				if(e.detail.value.length>6&&that.password.length>6){
					that.doneFlag=1;
				}else{
					that.doneFlag=0;
				}
			}else{
				that.seeAgainFlag=0;
				that.again='';
			}
		},
		see(flag){
			this.passwordFlag=flag;
		},
		seeAgain(flag){
			this.againFlag=flag;
		},
		complete(){
			var that = this;
			var password = that.password;
			var again = that.again;
			if(password.length>=6&&again.length>=6&&password==again){
				uni.showLoading({
					title:'加载中',
					mask:true,
					success() {
						uni.request({
						    url: that.serverUrl+'/user/setPsd', 
							method:'POST',
						    data: {
						       userId:uni.getStorageSync('userInfo').userId,
							   devCode:that.cid(),
							   password:password
						    },
						    header: {
						        'content-type': 'application/x-www-form-urlencoded'
						    },
							sslVerify:false,
						    success: (res) => {
								uni.hideLoading();
								if(res.data.resultCode=='10000'){
										uni.showToast({
											title: '设置成功',
											icon:'success',
											duration: 2000,
											mask:true
										});
										setTimeout(function () {
											uni.switchTab({
												url:'../information/information'
											})
										},2000);
								}else{
									that.errorMsg=that.showError(res.data.msgCode,res.data.msg);
									that.showFlag=1;
									setTimeout(function() {
										that.errorMsg='';
										that.showFlag=0;
									}, 5000);
								}
						    },
							fail:(re)=>{
								uni.hideLoading();
								uni.showModal({
								    title: '提示',
								    content: '网络异常，请检查网络后重试',
								    success: function (res) {
								        if (res.confirm) {
								            console.log('用户点击确定');
								        } else if (res.cancel) {
								            console.log('用户点击取消');
								        }
								    },
								});
							}
						});
						    
					}
				})
			}else{
				uni.showModal({
					title: '设置密码失败',
					content: '两次输入的密码不相同，请重新输入',
					success: function (res) {
					    if (res.confirm) {
					        console.log('用户点击确定');
					    } else if (res.cancel) {
					        console.log('用户点击取消');
					    }
					},
				})
			}
		}
	},
	onLoad(e) {
		this.phoneNum=e.phoneNum;
	},
	components:{
		back
	}
}
